<template>
  <div class="d-flex justify-center">

    <v-lazy>
      <v-hover v-slot="{hover}">
        <v-card class="ma-6 pa-4 article-card" max-width="1000" :elevation="hover?13:2"
                :href="'#'+$route.path+'/article/'+index">
          <v-card-title class="ml-2 article-title">
            <div>{{ title }}</div>
          </v-card-title>
          <v-card-text style="font-size: 9px;color: #948c9c" class="ml-2">{{ currentTime }}</v-card-text>
          <v-card-title>
            <v-chip v-for="(tab,index) in tabs" :color="colorList[index%6]" :key="tab"
                    class="ml-2"
            >{{ tab }}
            </v-chip>
          </v-card-title>
          <v-card-text class="ml-2" style="">{{ omit }}</v-card-text>
        </v-card>
      </v-hover>
    </v-lazy>
  </div>
</template>

<script>
export default {
  name: "ArticleList",
  props: {
    title: {
      type: String,
      default: "未接收到标题"
    },
    content: {
      type: String,
      default: "未接收到文章的内容"
    },
    tabs: {
      type: Array
    },
    index: {
      type: Number
    },
    display: {
      type: String
    }
  },
  data: () => ({
    colorList: ['default', 'primary', 'secondary', 'orange', 'red', 'green']
  }),
  computed: {
    currentTime() {
      let date = new Date().toDateString()

      return date
    },
    omit() {
      let cutStr = this.display.substr(0, 150) + "..."
      return cutStr
    }
  },
  methods: {}
}
</script>

<style scoped>
.article-title:hover {
  color: red;
  cursor: pointer;
}


</style>